/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Roboto", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
  color: #FFFFFF;
  font-weight: bold;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 5rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3.2rem;
}

p {
  font-size: 1.8rem;
}

.lateral {
  display: flex;
  flex-direction: column;
  width: 100%;
  height: 83rem;
  background-color: #484848;
  top: 0;
  left: 0;
}

@media (min-width: 1024px) {
  .lateral {
    height: 100%;
    width: 35rem;
    position: fixed;
    box-shadow: 10px 10px 16px -5px rgba(0, 0, 0, 0.1);
  }
}
.perfil {
  display: grid;
  place-content: center;
  margin: 5rem 0;
}
.perfil__avatar {
  width: 30rem;
  border-radius: 50rem;
  box-shadow: 10px 10px 16px -3px rgba(0, 0, 0, 0.1);
  transition: transform 3s;
}
.perfil__avatar:hover {
  transform: scale(1.3) rotate(360deg);
}
@media (min-width: 1024px) {
  .perfil__avatar {
    width: 20rem;
  }
}

.informacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
.informacion__nombre {
  color: #FFFFFF;
  font-size: 3rem;
  font-weight: 900;
  margin: 0;
}
@media (min-width: 1024px) {
  .informacion__nombre {
    font-size: 2rem;
  }
}
.informacion__parrafo {
  color: #FFFFFF;
  font-style: italic;
}

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 5rem 0;
  line-height: 4rem;
}
@media (min-width: 1024px) {
  .navegacion {
    margin: 2rem 0;
  }
}
.navegacion__link {
  font-weight: 900;
  font-size: 2rem;
  text-transform: uppercase;
  transition: all 0.3s;
}
.navegacion__link:hover {
  transform: scale(1.05);
  color: red;
}
@media (min-width: 1024px) {
  .navegacion__link {
    font-size: 1.2rem;
  }
}

.redes {
  display: flex;
}

.presentacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #8C8C8C;
}
@media (min-width: 1024px) {
  .presentacion {
    margin-left: 35rem;
    height: 100vh;
  }
}
.presentacion__titulo {
  margin: 5rem 0;
  color: #FFFFFF;
  font-weight: 900;
}
.presentacion__parrafo {
  padding: 0 5rem 5rem 5rem;
  margin: 0;
  color: #FFFFFF;
}

.educacion {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 1024px) {
  .educacion {
    margin-left: 35rem;
  }
}
.educacion__titulo {
  font-weight: 900;
}

.fase {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  padding: 1rem;
}
.fase__titulo {
  margin-bottom: 0;
}
.fase__contenido {
  margin-bottom: 5rem;
}

.referencias {
  background-color: #484848;
}
@media (min-width: 1024px) {
  .referencias {
    margin-left: 35rem;
  }
}
.referencias__titulo {
  text-align: center;
  padding: 1.5rem;
  color: #FFFFFF;
}

.grid {
  display: grid;
  grid-template-rows: repeat(3, 1fr);
}

.contenido {
  display: flex;
  background-color: #8C8C8C;
  margin: 0 2rem 5rem 2rem;
  height: 20rem;
  align-items: center;
  padding: 0 2rem;
  border-radius: 5rem;
  box-shadow: 10px 10px 18px 2px rgba(0, 0, 0, 0.1), 0px 10px 15px -3px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s;
}
.contenido:hover {
  transform: scale(1.05);
}
@media (min-width: 768px) {
  .contenido {
    margin: 0 5rem 5rem 5rem;
  }
}
@media (min-width: 1024px) {
  .contenido {
    width: 50rem;
    margin: 0 0 5rem 20rem;
  }
}
.contenido__imagen {
  height: 13.5rem;
  width: 13.5rem;
}
.contenido__nombre {
  color: #FFFFFF;
  font-size: 1.8rem;
  text-align: center;
  padding-left: 2rem;
  margin: 3rem 0 0 0;
  font-weight: 900;
}
@media (min-width: 768px) {
  .contenido__nombre {
    margin-left: 5rem;
    font-size: 2.5rem;
  }
}
@media (min-width: 1024px) {
  .contenido__nombre {
    margin-left: 0x;
  }
}
.contenido__ocupacion {
  color: #FFFFFF;
  text-align: center;
}